document.addEventListener( 'AC_Editing_Register_Editables', ( e ) => {
	let Editable = e.detail;
	let Abstract = Editable.editables._abstract;

	class Link extends Abstract {

		valueToInput( value ) {
			if ( !value ) {
				return;
			}

			this.getElement().querySelector( '[name=url]' ).value = value.url;
			this.getElement().querySelector( '[name=title]' ).value = value.title;

			if ( value.target === '_blank' ) {
				this.getElement().querySelector( '[name=target]' ).checked = true;
			}
		}

		getValue() {
			return {
				url : this.getElement().querySelector( '[name=url]' ).value,
				title : this.getElement().querySelector( '[name=title]' ).value,
				target : this.getElement().querySelector( '[name=target]' ).checked ? '_blank' : ''
			};
		}

		getTemplate() {
			const url = this._template.form.input( 'url', null, { placeholder : 'http://' } ).outerHTML;
			const title = this._template.form.input( 'title', null ).outerHTML;

			return `
				<div class="input__group">
					<label>Url</label>${url}
				</div>
				<div class="input__group">
					<label>Title</label>${title}
				</div>
				<div class="input__group -checkbox">
					<label class="input__checkbox"><input type="checkbox" name="target"> Open link in a new tab</label>
				</div>
			`;
		}

		_setDefaults() {
			this._defaults.type = 'acf_link';
		}
	}

	Editable.registerEditable( 'acf_link', Link );
} );

// Register MiddleWare
document.addEventListener( 'AC_Editing_Register_Middleware', ( e ) => {
	const MiddleWare = e.detail;
	const Abstract = MiddleWare.middleware.get( '_abstract' );

	class Link extends Abstract {
		getEditable() {
			let editable = MiddleWare.Editables.get( 'acf_link' );

			return editable ? editable : false;
		}
	}

	MiddleWare.register( 'acf_link', Link );
} );