const i18n = {
	REPLACE : ACP_Editing.i18n.replace_with,
	ADD : ACP_Editing.i18n.add,
	REMOVE : ACP_Editing.i18n.remove
};

const TEMPLATE = `
	<div class="input__group -type">
	<select class="type" name="save_strategy">
		<option value="replace">${i18n.REPLACE}..</option>
		<option value="add">${i18n.ADD}..</option>
		<option value="remove">${i18n.REMOVE}..</option>
	</select>
	</div>
`;

document.addEventListener( 'AC_Editing_Register_Editables', ( e ) => {
	let Editable = e.detail;
	let Abstract = Editable.editables.select2;

	class AcfSelect2 extends Abstract {

		render() {
			super.render();
			this.getElement().classList.add( '-taxonomy' );
			this.getElement().querySelector( '.aceditable__form__inputs' ).insertAdjacentHTML( "afterbegin", TEMPLATE );

			if ( this._args.mode === 'inline' || !this.isMultiple() ) {
				this.getTypeElement().style.display = 'none';
				this.getTypeElement().value = 'replace';
			} else {
				this.getTypeElement().style.display = 'block';
			}
		}

		isMultiple() {
			return this._args.select2.hasOwnProperty( 'multiple' ) && this._args.select2.multiple;
		}

		getTypeElement() {
			return this.getElement().querySelector( '[name=save_strategy]' );
		}

		getValue() {
			return {
				values : super.getValue(),
				save_strategy : this.getElement().querySelector( '[name=save_strategy]' ).value
			};
		}

		_setDefaults() {
			this._defaults.type = 'acf_select2';
		}

	}

	Editable.registerEditable( 'acf_select2', AcfSelect2 );
} );

// Register MiddleWare
document.addEventListener( 'AC_Editing_Register_Middleware', ( e ) => {
	const MiddleWare = e.detail;
	const Abstract = MiddleWare.middleware.get( 'select2_dropdown' );

	class AcfSelect2 extends Abstract {

		map() {
			return super.map();
		}

		getEditable() {
			let editable = MiddleWare.Editables.get( 'acf_select2' );

			return editable ? editable : false;
		}
	}

	MiddleWare.register( 'acf_select2', AcfSelect2 );
} );