document.addEventListener( 'AC_Editing_Register_Editables', ( e ) => {
	let Editable = e.detail;
	let Abstract = Editable.editables._abstract;

	class Range extends Abstract {

		render() {
			let slider = this.getElement().querySelector( '[name=range]' );

			this.reflect();
			slider.addEventListener( 'change', e => this.reflect() );
			slider.addEventListener( 'input', e => this.reflect() );
		}

		reflect() {
			this.getElement().querySelector( '[name=reflection]' ).value = this.getElement().querySelector( '[name=range]' ).value;
		}

		valueToInput( value ) {
			this.getElement().querySelector( '[name=range]' ).value = value;
			this.reflect();
		}

		getValue() {
			return this.getElement().querySelector( '[name=range]' ).value;
		}

		getTemplate() {
			let html_attributes = this._args.html_attributes;
			html_attributes.type = 'range';
			const range = this._template.form.input( 'range', null, this._args.html_attributes ).outerHTML;

			return `
				<div class="input__group">
					${range}
					<input name="reflection">
				</div>
			`;
		}

		_setDefaults() {
			this._defaults.type = 'acf_range';
			this._defaults.html_attributes = {
				step : 5,
				min : 0,
				max : 100
			};
		}
	}

	Editable.registerEditable( 'acf_range', Range );
} );

// Register MiddleWare
document.addEventListener( 'AC_Editing_Register_Middleware', ( e ) => {
	const MiddleWare = e.detail;
	const Abstract = MiddleWare.middleware.get( 'default' );

	class Range extends Abstract {

		map() {
			return super.map();
		}

		getEditable() {
			let editable = MiddleWare.Editables.get( 'acf_range' );

			return editable ? editable : false;
		}
	}

	MiddleWare.register( 'acf_range', Range );
} );